import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

//后面这里引入可以被用户访问的页面组件
import Home from '../components/Home'
import LibraryIndex from '../components/library/LibraryIndex'
import AppIndex from '../components/home/AppIndex'

import Chapter from '../components/library/Chapter'
import BooksDemo from "../components/library/BooksDemo"
import SideMenu from '../components/library/SideMenu'
import errPage from '../components/404'


Vue.use(Router)

export default new Router({
  //路由跳转，去除#号
  mode: 'history',
/*  mode:'hash',*/
  //路由列表
  routes: [
    {
      path:'*',
      component: errPage
    },
    {
      //路由地址
      path: '/',
      //路由别名
      name: 'Home',
      //组件类名
      component: Home,
      // redirect:'/booksDemo',
      //home页面不需要被访问
      children:[{
        path: 'SideMenu',
        component: SideMenu
      },{
        path:'BooksDemo',
        name: BooksDemo,
        component: BooksDemo,
        props: true
      }
      ]
    },{
      path: '/chapter',
      component:Chapter,
      props: (route)=>({query:route.query.id})
    }
  ]
})



