<template>
  <el-container>
    <el-aside style="width: 200px;margin-top: 20px">
      <switch></switch>
      <!--侧边栏组件-->
      <SideMenu></SideMenu>
    </el-aside>
    <el-main>
      <!--books组件-->
      <books class="books-area"></books>
    </el-main>
  </el-container>
</template>

<script>
  import SideMenu from './SideMenu'
  import Books from './Books'
   export default {
       name: "AppLibrary",
       components: {SideMenu,Books}
   }
</script>

<style scoped>
  .books-area {
    width: 990px;
    margin-left: auto;
    margin-right: auto;
  }
</style>



