<template>

    <el-container>
      <el-header>
        <!--  <a id="toHome" @click="toHome">首页</a>-->
        <el-row id="header">
          <el-col :span="3" style="width: 13%;">
            <a @click="toHome" id="toHome" href="">全部小说</a>
          </el-col>
          <el-col :span="17">
            <span style="color: #157892; position: absolute;padding-top: 10px;padding-left: 50px;padding-right: 50px;">
            声明：该网站只用做个人测试，不涉及任何商业用途，网页内所有链接全部来自于
            <a href="https://www.52bqg.net/">笔趣阁</a>，
            本站只录入小说信息，并不保存章节内容，所有章节链接只是一个跳转，即本站只做内容跳转，
            如有造成不良影响，请联系作者删除，作者Q:1291516667</span>
          </el-col>
        </el-row>
      </el-header>
     <!-- <p>{{bookId}}</p>-->
      <el-main>
        <el-card class="box-card-bookInfo">
          <div  class="text item">
            <el-row>
              <el-col :span="4">
                <div class="book-img"  style="width: 180px;height: 250px;"  >
                  <img :src="book.imgURL" style="width: 170px;height: 230px;" alt="封面">
                </div>
              </el-col>

              <el-col :span="20" style="padding-left: 10px">
                <a href="" style="margin-bottom: 210px;margin-bottom: 10px">{{book.bookName}}</a>
                <p href="" style="margin-bottom: 10px">作者： {{book.author}}</p>
                <p class="book-info" href="" style="margin-bottom: 10px">{{book.info}}</p>
              </el-col>
            </el-row>
            <br/>
          </div>
        </el-card>

        <el-card class="box-card-chapter">
          <div  class="text item">
            <el-row>
              <el-col class="chapterName" :span="8" v-for="chapter in chapterList" :key="chapter.id">
               <a :href="chapter.chapterURL" target="_blank">{{chapter.chapterName}}</a>
              </el-col>

            </el-row>
            <br/>
          </div>
        </el-card>

      </el-main>
      <el-footer>
        <hr/>
        <div style="width:300px;margin:0 auto; padding:20px 0;">
          <span style="position: relative;top: -40px;"><img src="../../assets/备案图标.png"></span>
          <a target="_blank" href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=42011102003981" style="display:inline-block;text-decoration:none;height:20px;line-height:20px;"><img src="" style="float:left;"/><p style="float:left;height:20px;line-height:20px;margin: 0px 0px 0px 5px; color:#939393;">鄂公网安备 42011102003981号</p></a>
        </div>
      </el-footer>
    </el-container>

</template>

<script>
    export default {
        name: "Chapter",
        data(){
          return{
            bookId:[],
            book:[],
            chapterList:[]
            /*chapter:[]*/
          }
        },
      methods:{
          toHome(){
            this.$router.push({path:'/'})
          },
          getBookId(){
            this.bookId = this.$route.query.id
          },
          getBook(){
            var _this = this
            _this.$axios.get('/books/getBookById/'+this.bookId).then(resp=>{
              this.book = resp.data
            })
          },
          getChapter(){
            var _this = this
            _this.$axios.get('/chapter/getChapterByBookName/'+this.bookId).then(resp=>{
              this.chapterList = resp.data
            })
          }

          /*//接受参数
          getParams(){
            console.log(this.$router)
            this.book= this.$router.current.query.name;
          }*/
      },
      created() { //在模板编译进路由前调用created方法，触发接受函数
        this.getBookId()
        this.getBook()
        this.getChapter()

        /*var self = this;
        self.getParams();*/
      }

    }
</script>


<style scoped>

  .chapterName{
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .box-card-bookInfo {
    margin: 0px;
    padding: 0px;
    margin-left: 10%;
    width: 80%;
    /* border: 1px solid #000000;*/
    height: 12%;
  }
  .box-card-chapter {
    margin: 0px;
    padding: 0px;
    margin-left: 10%;
    width: 80%;
    /* border: 1px solid #000000;*/
    height: 100%;
  }

  a{

    /*去除下划线*/
    text-decoration: none;
    color: #094FAE;
    padding-bottom: 50px;
  }
  p{
    padding-top: 10px;
    margin: 0px;
  }
  .book-info{
    font-size: 17px;
    padding-bottom: 4px;
    margin: 0px;
    width: 100%; /* //宽度根据你要显示的字数来定*/
    height:60px;

  }

/*  内容框架*/
  #header{
    border-bottom: 1px solid #333333;

    height: 80px;
  }


  #toHome{
    font-size: 40px;
  }
</style>



