<template>
    <div>


<!--
      <p>{{bookTypeId}}haha</p>
-->

      <el-card class="box-card" v-for="book in books" :key="book.id">
        <div  class="text item">
          <el-row>
            <el-col :span="4">
              <div class="book-img"  style="width: 120px;height: 150px;"  >
                <img :src="book.imgURL" style="width: 120px;height: 150px;" alt="封面">
              </div>
            </el-col>
            <el-col :span="20">
              <a href="" >{{book.bookName}}</a>
              <p href="" >作者： {{book.author}}</p>
              <p class="book-info" href="">{{book.info}}</p>
              <!--<div class="info" style="border: 1px solid #333333; width: 100%;height: 120px">
              </div>-->
              <el-col :span="4">
                <!--<router-link :to="{name:'chapter',params:{book:this.book}}">bookName</router-link>-->
                <!--<router-link round style="display: inline-block;margin-left: 750px;"
                          :to="{path:'chapter',params:{id:book.id}}">
                  章节目录</router-link>-->
                <el-button  round style="display: inline-block;margin-left: 750px;"  @click="goChapter(book.id)">章节目录</el-button>
              </el-col>
            </el-col>
          </el-row>
      </div>
      </el-card>
      <el-row>
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="pageSize"
            :total="totalPage"
           >
          </el-pagination>

       <!-- <el-pagination
          @current-change="handleCurrentChange"
          :current-page="books.currentPage"
          :page-size="books.pagesize"
          :page-count="books.pages"
          :total="books.total">
        </el-pagination>-->
      </el-row>
   <!--   <el-row>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pagesize"
          :total="books.length">
        </el-pagination>
      </el-row>-->
    </div>
</template>

<script>
  import bus from '../bus/bus'
  import Chapter from './Chapter'
    export default {
        name: "BooksDemo",
      props: ['typeId'],
      components:{
          Chapter
      },
      data(){
        return{
          books:[],
          bookTypeId: 0,
          currentPage:1,
          pageSize:5,
          totalPage: []
        /*  pagesize:5,
          currentPage:1*/
        }
      },
      watch:{
          typeId:{
            handler(newVal,oldVal){
            this.bookTypeId=newVal;
            console.log("新值："+this.bookTypeId+",旧值"+oldVal)
          },immediate:true
          },/*,
          typeId:function (newVal,oldVal) {
            this.bookTypeId=newVal;
            console.log("新值："+this.bookTypeId+",旧值"+oldVal)
          },*/
          bookTypeId:function(newVal,oldVal){
            this.getBooksById(newVal)
          },immediate: true,

      },
      created:function(){
        this.getBooksById(bookTypeId)

      },
      mounted:function () {
        this.getBooks()

      },
      methods:{
        getBooks(){
          var _this = this
          _this.$axios.get('/books/getBooksAll').then(resp=>{
            this.books = resp.data.records
            this.currentPage = resp.data.current
            this.totalPage = resp.data.total
          })
        },
        getBooksById:function(bookTypeId){
          var _this = this
          bookTypeId = this.bookTypeId
            _this.$axios.get('/books/getBookByTypeName/'+bookTypeId,{
              params:{
                index: this.currentPage,
                pageSize: this.pageSize
              }

          }).then(resp=>{
              this.books = resp.data.records
              this.currentPage = resp.data.current
              this.totalPage = resp.data.total
            })
          },
        goChapter(bookId){

         this.$router.push({path:'/chapter',query:{id:bookId}})
      },
        handleCurrentChange:function (currentPage) {
          this.currentPage = currentPage
          this.getBooksById(currentPage)
        }
      }
      
    }
</script>

<style scoped>

  .box-card {
    margin: 0px;
    padding: 0px;
    width: 100%;
   /* border: 1px solid #000000;*/
    height: 12%;
  }
  a{
    /*去除下划线*/
    text-decoration: none;
    color: #094FAE;
  }
  p{
    padding-bottom: 6px;
    margin: 0px;
  }
  .book-info{
    font-size: 17px;
    padding-bottom: 4px;
    margin: 0px;
    width: 80%; /* //宽度根据你要显示的字数来定*/
    height:60px;
    overflow: hidden;
    white-space: normal;
    text-overflow: ellipsis;
    /*overflow:hidden;
    text-overflow:ellipsis;
    display:-webkit-box;
    -webkit-box-orient:vertical;
    -webkit-line-clamp:4;*/
    /*overflow:hidden;
    text-overflow:ellipsis;
    display:-webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp:3;*/
  }
/*  .book-img{
    width: 115px;
    height: 172px;
    margin-bottom: 7px;
    overflow: hidden;
    cursor: pointer;
  }*/

  /* 书框*/
/*  .book-img {
    !*float: left;*!
    display: inline-block;
    width: 115px;
    height: 172px;
    margin-top: 0px;
    padding-top: 0px;
    margin-bottom: 7px;
    overflow: hidden;
    cursor: pointer;
  }

  img {
    width: 115px;
    height: 172px;
    !*margin: 0 auto;*!
  }*/


</style>



